<template>
  <div>
    <s-login v-if="!isAuthenticated"/>
    
    <div v-else class="wrapper">
      <s-navbar />      
      <main class="main">
        <s-header />
        <div class="main__wrapper">
          <router-view />
        </div>
      </main>
    </div>

  </div>  
</template>

<script>
import {mapState} from 'vuex'

import sLogin from './login/Login'
import sHeader from './layouts/Header'
import sNavbar from './layouts/Navbar'

export default {
  components: {
    sLogin,
    sHeader,
    sNavbar
  },
  computed: {
    ...mapState(['user']),
    isAuthenticated() {
      return !!this.user
    }
  }
}
</script>

<style lang="scss">
.wrapper {
  height: 100vh;
  display: flex;
  justify-content: space-between;
  color: #fff;
}
.main {
  padding: 0 2rem;
  width: 80%;
  background-color: #424568;
  &__wrapper {
    font-size: 2rem;
    margin-top: 2rem;
  }
}
</style>