import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import Efficiency from '../views/Efficiency.vue'
import Normative from '../views/Normative.vue'
import Archive from '../views/Archive.vue'
import Settings from '../views/Settings.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/efficiency',
    name: 'Efficiency',
    component: Efficiency
  },
  {
    path: '/normative',
    name: 'Normative',
    component: Normative
  },
  {
    path: '/archive',
    name: 'Archive',
    component: Archive
  },
  {
    path: '/Settings',
    name: 'Settings',
    component: Settings
  },
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
