import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    user: null,

    users: [
      { name: 'Худалов М.З.', email: 'hmz@mail.ru', password: '123', faculty: 'Факультет математики и компьютерных наук', facultyLogo: 'header-logo-it.svg'},
      { name: 'Такаев З.И.', email: 'zatakata1998@gmail.com', password: '123', faculty: 'Факультет искусств', facultyLogo: 'header-logo-art.svg'}
    ]
  },
  mutations: {
    SET_USER(state, user) {
      state.user = user
    },
    REMOVE_USER(state) {
      state.user = null
    }
  },
  actions: {
    SIGN_IN({commit, state}, {email, password}) {
      const foundUser = state.users.find(u => u.email === email && u.password === password)
      if (foundUser) {
        commit('SET_USER', foundUser)
      }
    },
    LOGOUT({commit}) {
      commit('REMOVE_USER')
    }
  },
  modules: {
  }
})
