<template>
  <transition name="fade">
    <div class="login-form login__form" v-if="activeLink === 'login'">
      <form class="login-form__wrapper" @submit.prevent="signIn">
        <div class="el-input">
          <label for="email">Введите Email адрес</label>
          <input type="email" v-model="email" placeholder="Email" id="email">
        </div>
        <div class="el-input">
          <label for="password">Введите пароль</label>
          <input type="password" v-model="password" placeholder="Пароль" id="password">
        </div>
        <button class="login-form__submit" type="submit">
          Войти
          <div class="login-form__submit-icon">
            <svg xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" stroke="currentColor">
              <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M13 9l3 3m0 0l-3 3m3-3H8m13 0a9 9 0 11-18 0 9 9 0 0118 0z" />
            </svg>
          </div>
        </button>
        <div class="login-form__footer">
          <a href="#" class="login-form__forgot">Забыли пароль?</a>
          <div class="login-form__remember">
            <label for="remember">Не выходить из системы</label>
            <input type="checkbox" id="remember">
          </div>
        </div>
      </form>
    </div>
  </transition>
</template>

<script>
import {mapActions} from 'vuex'

export default {
  props: ['activeLink'],
  data: () => ({
    email: '',
    password: '',
    
    
  }),
  methods: {
    ...mapActions(['SIGN_IN']),
    signIn() {
      const formData = {
        email: this.email,
        password: this.password
      }

      this.SIGN_IN(formData)
    },
    logout() {
    }
  }
}
</script>

<style lang="scss">
.fade-enter-active, .fade-leave-active {
  transition: transform .7s;
}

.fade-enter, .fade-leave-to {
  transform: translateX(100%);
}

.login-form {
  padding: 10em 5em;
  background-color: #fff;
  &__wrapper {
    width: 100%;
  }
  &__submit {
    width: 10em;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: .5em;
    padding: 0.7em 1em 0.7em 3em;
    font-size: 1.3em;
    line-height: 1em;
    background-color: #ccc;
    border: none;
    border-radius: 1em;
    outline: none;
    background-color: #1a204f;
    color: #fff;
    cursor: pointer;
    transition: .3s;
    &:hover {
      opacity: .9;
    }
    &-icon {
      margin-left: .3em;
      width: 26px;
      height: auto;
    }
  }
  &__footer {
    margin-top: 1.5em;
    display: flex;
    justify-content: space-between;
    font-size: 1.2em;
  }
  &__forgot {
    color: #1a204f;
  }
  &__remember {
    display: flex;
    align-items: center;
    label {
      cursor: pointer;
      user-select: none;
    }
    input[type=checkbox] {
      margin-left: .5em;
      width: 1.5em;
      height: 1.5em;
      cursor: pointer;
    }
  }
}

.el-input {
  margin-bottom: .7em;
  display: flex;
  flex-direction: column;
  label {
    margin-bottom: .4em;
    font-size: 1.3em;
  }
  input {
    width: 70%;
    padding: 1.3em;
    background-color: #f5f6f9;
    border: none;
    border-radius: 1em;
    outline: none;
    &::placeholder {
      font-size: 1.1em;
    }
    &:focus {
      background-color: #406b8a;
      color: #fff;
      &::placeholder {
        color: #fff;
      }
    }
  }
}
</style>